import {
  Card,
  CardHeader,
  CardContent,
} from "../../../../../components/Elements/cards/Card";
import { Badge } from "../../../../../components/Elements/Badge";
import { useRouter } from "next/router";
import { useEffect, useState } from "react";
import { CandidateCard } from "@/components/Elements/cards/candidateCard";
import MainLayout from "@/components/Layouts/layout";
import { Popup } from "@/components/Elements/cards/popup";
import { JobCard } from "@/components/Elements/cards/jobCard";
import { Round } from "@/lib/models/candidate";
import InterviewForm from "@/components/Forms/updateInterview";
import AddRound from "@/components/Forms/addInterview";
import { Candidate } from "@/lib/definitions";
import { fetchCandidate } from "@/api/candidates/candidates";
import { fetchJob } from "@/api/client/clientJob";
import { Client } from "@/lib/models/client";
import { fetchInterviewRoundsByContact } from "@/api/interviews/InterviewRounds";
export default function CandidateInterviews() {
  const router = useRouter();
  const Id = Number(router.query.interviewid);
  const candidateId = Number(router.query.id);
  const [addRoundEnabled, setAddRoundEnabled] = useState(false);
  const [updateRoundEnabled, setUpdateRoundEnabled] = useState(false);
  const [currentCandidate, setCurrentCandidate] = useState<Candidate | null>(
    null
  );
  const [currentJobData, setCurrentJobData] = useState<Client | null>(null);
  const [allRounds, setAllRounds] = useState<Round[] | null>(null);

  useEffect(() => {
    fetchCandidate(candidateId)
      .then((data) => {
        // console.log(data);
        setCurrentCandidate(data);
      })
      .catch((err) => {
        console.log(err);
      });
    fetchJob(Id).then((data) => {
      setCurrentJobData(data);
    });
    fetchInterviewRoundsByContact(candidateId).then((data) => {
      setAllRounds(data);
    });
  }, [allRounds, ]);
  return (
    <MainLayout>
      <div className="space-y-6">
        {/* Profile Info Section */}
        <section>
          <h2 className="text-xl font-semibold mb-4">Profile Info</h2>
          {/* Candidate Card needed Here */}
          <CandidateCard candidateData={currentCandidate} />
        </section>

        {/* Job Info Section */}
        <section className="space-y-4">
          <h3 className="text-xl font-semibold">Job Info</h3>
          <JobCard jobData={currentJobData} isClient={false} />
        </section>

        {/* Interviews Section */}
        <section className="space-y-8">
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-xl font-semibold">Interviews</h2>
            <div className="flex items-center gap-2">
              <span className="text-gray-600 font-semibold">
                Over all status
              </span>
              <Badge variant="success">
                <p>Passed</p>
              </Badge>
            </div>
          </div>
          {allRounds?.map((rounds, index) => (
            <div>
              <Card className="p-4">
                <CardHeader className="mb-4">
                  <h3 className="font-semibold text-lg">
                    Round {rounds.roundNumber}
                  </h3>
                  <button
                    className="bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 rounded text-base"
                    onClick={() => setUpdateRoundEnabled(true)}
                  >
                    Update
                  </button>
                </CardHeader>
                <CardContent className="bg-white border shadow-sm p-4">
                  <div className="flex justify-between items-start mb-6">
                    <div className="flex items-center gap-12">
                      <div>
                        <h4 className="font-semibold text-lg">
                          {rounds.roundName}
                        </h4>
                        <p className="text-sm text-gray-500">
                          {rounds.roundDate}
                        </p>
                      </div>
                    </div>

                    <Badge variant="success">{rounds.interviewStatus}</Badge>
                  </div>

                  <div className="grid grid-cols-2 gap-6 md:grid-cols-4">
                    <div>
                      <p className="text-sm text-black font-semibold">
                        Interviewer Name
                      </p>
                      <p className="text-gray-700">{rounds.interviewerName}</p>
                    </div>
                    <div>
                      <p className="text-sm text-black font-semibold">
                        Technology Interviewed
                      </p>
                      <p className="text-gray-700">
                        {rounds.technologyInterviewed}
                      </p>
                    </div>
                    <div>
                      <p className="text-sm text-black font-semibold">
                        Tech Rating
                      </p>
                      <p className="text-gray-700">{rounds.techRating}/10</p>
                    </div>
                    <div>
                      <p className="text-sm text-black font-semibold">
                        Soft skill Rating
                      </p>
                      <p className="text-gray-700">
                        {rounds.softskillsRating}/10
                      </p>
                    </div>
                  </div>

                  <div className="mt-4">
                    <p className="text-sm text-black font-semibold">Remarks</p>
                    <p className="text-gray-700">{rounds.remarks}</p>
                  </div>
                </CardContent>
              </Card>
              {updateRoundEnabled && (
                <Popup onClose={() => setUpdateRoundEnabled(false)}>
                  <InterviewForm
                    round={rounds}
                    roundNo={index + 1}
                    className="bg-white relative mt-12"
                  />
                </Popup>
              )}
            </div>
          ))}
          <div className="flex justify-end mt-8">
            <button
              className="bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 rounded"
              onClick={() => setAddRoundEnabled(true)}
            >
              Add New Round
            </button>
          </div>
        </section>
        {addRoundEnabled && (
          <Popup onClose={() => setAddRoundEnabled(false)}>
            {allRounds?.[0].interview && allRounds?.[0].interview.contactDetails && (
              <AddRound
                className="mt-10 rounded-md bg-white m-8"
                interviewInfo={{
                  interview: allRounds[0].interview,
                  contactDetails: allRounds[0].interview.contactDetails,
                }}
                onclose={() => setAddRoundEnabled(false)}
              ></AddRound>
            )}  
          </Popup>
        )}
      </div>
    </MainLayout>
  );
}
